import {Component, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
declare var $: any;


@Component({
  selector: 'app-dash',
  templateUrl: './appDash.component.html',
  styleUrls: ['./appDash.component.css']
})
export class AppDash implements OnInit {
  constructor( private RutinAct: ActivatedRoute, private Rutin: Router) {

  };

  ngOnInit() {
    $(document).ready(function() {
      setTimeout(()=>{
        $('header, main, footer').fadeIn(1000);
      }, 500);
      $('.appFondo').css('display', 'block');
      $('.button-collapse').sideNav({
        menuWidth: 260, // Default is 300
        edge: 'left', // Choose the horizontal origin
        closeOnClick: true, // Closes side-nav on <a> clicks, useful for Angular/Meteor
        draggable: true, // Choose whether you can drag to open on touch screens,
        onOpen: function (event) {
        }, // A function to be called when sideNav is opened
        onClose: function (event) {
        },  // A function to be called when sideNav is closed
      });
      $('.collapsible').collapsible();
      $('.dropdown-button').dropdown({
        hover: true,
        gutter: 0, // Spacing from edge
        belowOrigin: true,
        stopPropagation: true
      });
      $('main').css('height',$(window).height()-75);
      $(window).on('resize', function (){
        $('main').css('height',$(window).height()-75);
      });
    });
    setTimeout(() => {
      this.moveramenus();
    }, 1000);
  };
  private moveramenus(): void {
    this.Rutin.navigate(['menus'], { relativeTo: this.RutinAct });
  }
}



