import { Injectable } from '@angular/core';
import {HttpClient, HttpErrorResponse} from '@angular/common/http';
// import {Observable} from 'rxjs/Observable';
import {ErrorObservable} from 'rxjs/observable/ErrorObservable';
import { catchError } from 'rxjs/operators';
import {CONFIG} from '../../configuraciones/config';


@Injectable()
export class LoginService {
  cabezeras: object = {};
  constructor(private http: HttpClient) {
    this.cabezeras = {
      headers: {
        'Content-Type':  'application/json ; charset=utf-8',
        'Authorization': 'my-auth-token'
      },
      observe:  'response',
      responseType: 'json', // 'json',
    };

  }

  private controlarError(error: HttpErrorResponse) {
    let tipoError: number;
    if (error.error instanceof ErrorEvent) {
      // Ocurrio un error del lado del cliente o en la red.
      console.error('lo que sucedio fue:', error.error.message);
      tipoError = 1;
    } else {
      // El servidor devolvió un código de respuesta fallido.
      // El cuerpo de respuesta puede contener pistas sobre lo que salió ma
      console.error(
        `codigo proveniente del backend: ${error.status}, ` +
        `cuerpo de la respuesta: ${error.error}`);
      tipoError = 2;
    }
    // Devuelve un ErrorObservable con un mensaje de error que mira al usuario
    if ( tipoError === 1) {
      return new ErrorObservable(
        'Hay un error en su red, varifique si tiene conexion a internet, y si su navegador esta configurado de manera adecuada .');

    } else if (tipoError === 2) {
      return new ErrorObservable(
        'Hay un error del lado del servidor, algun proceso ha salido mal, o no hay acceso al material solicitado .');

    }
  }

  public validarlogin(valor: object){
    // const json: string = 'json=' + JSON.stringify(valor);
    return this.http.post(CONFIG.urlLoc + '/hola', valor, this.cabezeras)
      .pipe(
        catchError(this.controlarError)
      );
  }

}



