import { NgModule } from '@angular/core';
import {Routes, RouterModule, UrlSegment} from '@angular/router';
import {Principal1Component} from './menus-principal1/principal1.component';
import {AppDash} from './appDash.component';



const routes: Routes = [
  {
    path: '',
    redirectTo: 'dash',
    pathMatch: 'full'
  },
  {  path: 'dash',
     component: AppDash,
     children: [
      {
        path: 'menus',
        component: Principal1Component
      }
    ]
  }
];

/*const routes: Routes = [
  {
    path: '',
    component: AppDash
  },
  {
    path: 'menus',
    component: Principal1Component
  }
  ];
*/
@NgModule({
  imports: [
    RouterModule.forChild(routes),
  ],
  exports: [
    RouterModule
  ],
  declarations: [
  ]
})
export class PanelControlRoutingModule { }




