import { EntradaRoutingModule } from './entrada-routing.module';
import {ManejadortokenService} from '../service/tokens/manejadortoken.service';
import { LoginService } from '../service/login/login.service';

@NgModule({
  imports: [
    FormsModule,
    EntradaRoutingModule,
    HttpClientModule
  ],
  declarations: [
    AppInicio,
    LoginComponent
  ],
  providers: [
    ManejadortokenService,
    LoginService
  ]
})
export class EntradaModule {

}



